

 /*---- Video Slideshow -----*/
 .video-container {
  position: relative;
  width: 100%;
  height: 70vh;
}

.video video {
    position: absolute;
    top: 0;
    left: 0;
    width:100%;
    height: 90%;
    object-fit: cover;
}

