.mainQuote {
  z-index: 1;
  font-family: 'Poppins', sans-serif !important;
  position:absolute;
  font-size: 20px;
  top:60%;
  left:5%;
  text-align: center;
  color: #fff;
  background-color: #ca6f0079;
 padding: 5px;
}


/*---- Recommended Products Grid ----*/

#recProd{
  color:#fff;
  text-align: center;
  margin-top: 3%;
  border-style: solid;
  border-color: #ca6f00;
}
.grid-container {
    display: grid;
    text-align: center;
    justify-content: center;
    grid-template-columns:300px 300px 300px;
    grid-template-rows: auto;
    margin-top: 40px;
    column-gap: 40px;
    height: 50vh;
}

.grid-item img {
    width: 300px;
    height: 300px;
    text-align: center;
}

.grid-item {
    padding: 20px;
    color: white;
    cursor: pointer;
}

.grid-item p {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  color:#ca6f00;
}

/*---- Motivation Text ----*/
.motivation {
    color: #ca6f00;
    /* display: inline-block; */
    text-align: center;
    font-size: 60px;
    padding: 15%;
    /* margin: 10%;  */
    display: block;
    margin-left: auto;
    margin-right: auto;
    background-image: url('/public/images/dark_lift.jpg');
    background-attachment: fixed;
    background-size: cover;
}

.learn-container{
    padding-top: 100px;
    padding-bottom: 100px; 
}

.info{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    justify-items: center;
    padding: 50px;
}

.info-image {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    align-content: stretch;
  
}

.learn-text {
    display: flex;
    align-items: center;
    flex-direction: column;
    align-content: stretch;
    color: white;
    padding: 30px;
  /* z-index: 100;
  position: absolute;
