  border-color: #ca6f00; */
}

.learn-text h2 {
    text-align: center;
    letter-spacing: 1px;
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 30px;
   
  }

  
.learn-text p {
    text-align: center;
    font-size: 15px;
    padding-bottom: 10px;
    font-family: 'Poppins', sans-serif;
  }

.learn-text button {
  height: 40px;
  width: 200px;
  margin: 20px;
  border:none;
  letter-spacing: 1px;
  background-color: #ca6f00;
  color: #fff;
}

.learn-text button:hover{
  background-color: #fff;
  color: #cb7102;
}


/*---- Responsive ----*/

/*--- Small Mobile -----*/
@media screen and (min-width: 320px) and (max-width: 480px){
  * {
    overflow-x: hidden;
}

.mainQuote {
  position:relative;
  font-size: .7rem;
  top: -300px;
  z-index: 1;
  left: 0;
}

#recProd{
  color:#fff;
  text-align: center;
  margin-top: 5%;
  border-style: solid;
  border-color: #ca6f00;
}

.grid-container {
  display: flex;
  flex-direction: column;
  height: 60%;
}

.grid-item img {
  width: 100px;
  height: 100px;
  text-align: center;
}

.motivation {
  font-size: 20px;
  padding: 15%;
}

.info{
  display: flex;
  flex-direction: column;
}

.info-image img {
  height: 20vh;
}

.learn-text h2 {
  font-size: 15px;
}

.learn-text p {
  font-size: 10px;
}
}
  /* ---------- tablets ----------- */
@media screen and (min-width: 481px) and (max-width: 768px){
  * {
      overflow-x: hidden;
      
  }

  .mainQuote {
    font-size: .9rem;
    position:relative;
    top: -300px;
    z-index: 1;
    left: 0;
  }
  
  #recProd{
    color:#fff;
    text-align: center;
    margin-top: 5%;
    border-style: solid;
    border-color: #ca6f00;
  }
  
  .grid-container {
    display: flex;
    flex-direction: column;
    height: 60%;
  }
  .grid-item img {
    width: 100px;
    height: 100px;
    text-align: center;
  }
  
  .motivation {
    font-size: 20px;
    padding: 15%;
  }
  

.info{
  display: flex;
  flex-direction: column;
}

  .learn-text h2 {
    font-size: 20px;
  }

  .learn-text p {
    font-size: 13px;
  }
};

